import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
import {getStoreInfo,collectStoring,unCollectStore} from "@/network/detail"

// 导入vuex
import { mapGetters } from 'vuex'

import {replaceImage} from '@/utils/dealUrl'
export default{
	components: {
		uniLoadMore
	},
	data(){
		return{
			loadingimg:true,//login加载
			loadingType:1,//login状态
			currentTab: 0,//导航栏状态
			shopselect:0,//综合  销量 价格
			priceType:true,// true 价格升序  false价格降序
			isCollectIng:false, //是否收藏
			storeId:'',
			storeInfo:{},
			tabs2: [{
				name: "今日特拼"
			}, {
				name: "全部"
			}, {
				name: "新品"
			}, {
				name: "新品",
			}],
			productList: [{
					img: 1,
					name: "欧莱雅（LOREAL）奇焕光彩粉嫩透亮修颜霜 30ml（欧莱雅彩妆 BB霜 粉BB 遮瑕疵 隔离）",
					sale: 599,
					factory: 899,
					payNum: 2342
				},
				{
					img: 2,
					name: "德国DMK进口牛奶  欧德堡（Oldenburger）超高温处理全脂纯牛奶1L*12盒",
					sale: 29,
					factory: 69,
					payNum: 999
				},
				{
					img: 3,
					name: "【第2支1元】柔色尽情丝柔口红唇膏女士不易掉色保湿滋润防水 珊瑚红",
					sale: 299,
					factory: 699,
					payNum: 666
				},
				{
					img: 4,
					name: "百雀羚套装女补水保湿护肤品",
					sale: 1599,
					factory: 2899,
					payNum: 236
				},
				{
					img: 5,
					name: "百草味 肉干肉脯 休闲零食 靖江精制猪肉脯200g/袋",
					sale: 599,
					factory: 899,
					payNum: 2399
				},
				{
					img: 6,
					name: "短袖睡衣女夏季薄款休闲家居服短裤套装女可爱韩版清新学生两件套 短袖粉色长颈鹿 M码75-95斤",
					sale: 599,
					factory: 899,
					payNum: 2399
				},
				{
					img: 1,
					name: "欧莱雅（LOREAL）奇焕光彩粉嫩透亮修颜霜",
					sale: 599,
					factory: 899,
					payNum: 2342
				},
				{
					img: 2,
					name: "德国DMK进口牛奶",
					sale: 29,
					factory: 69,
					payNum: 999
				},
				{
					img: 3,
					name: "【第2支1元】柔色尽情丝柔口红唇膏女士不易掉色保湿滋润防水 珊瑚红",
					sale: 299,
					factory: 699,
					payNum: 666
				},
				{
					img: 4,
					name: "百雀羚套装女补水保湿护肤品",
					sale: 1599,
					factory: 2899,
					payNum: 236
				}
			],
		}
	},
	onReachBottom(){
		
		
	},
	onLoad(option) {
		if(option.id && !!this.isToken){
			this.storeId = option.id
		}else{
			uni.switchTab({
				url:'../../Home/home'
			})
			return false
		}
		// 调用数据接口
		this.getStoreInfo(this.storeId,this.isToken)
	},
	methods:{
		// 获取信息
		getStoreInfo(id,token){
			getStoreInfo(id,token).then(res => {
				if(res.data.code == 200){
					this.isCollectIng = res.data.data.user_collect
					this.storeInfo = res.data.data
				}
			})
		},
		// 店铺收藏
		collectStore(){
			// 如果未收藏
				if(!this.isCollectIng){
					collectStoring(this.storeId,this.isToken)
					.then(res => {
						if(res.data.code == 200){
							uni.showToast({
								title:'收藏成功',
								icon:'none'
							})
							this.isCollectIng = !this.isCollectIng
						}
					})
				}else{
					// 如果已经收藏
					unCollectStore(this.storeId,this.isToken)
						.then(res => {
							if(res.data.code == 200){
								uni.showToast({
									title:'已取消收藏',
									icon:'none'
								})
								this.isCollectIng = !this.isCollectIng
							}
						}).catch(err => {
							console.log(err)
						})
				}
			
			
		},
		//首页
		HomeClick(){
			if(this.currentTab == 0) return
			this.currentTab = 0
		},
		//商品
		shopClick(){
			if(this.currentTab == 1) return
			this.currentTab = 1
			this.shopselect = 0
		},
		//促销
		activtyClick(){
			if(this.currentTab == 2) return
			this.currentTab = 2
		},
		//新品
		newShopClick(){
			if(this.currentTab == 3) return
			this.currentTab = 3
		},
		//综合
		syndClick(){
			if(this.shopselect = 0) return
			this.shopselect = 0
		},
		//销量
		salesClick(){
			if(this.shopselect = 1) return
			this.shopselect = 1
		},
		//价格
		priceClick(){
			this.shopselect = 2
			if(this.priceType){
				//价格升序
				this.priceType = false
			}else{
				//价格降序
				this.priceType = true
			}
		},
		px(num) {
			return uni.upx2px(num) + "px"
		},
		//顶部导航栏搜索
		searchClick(){
			
		},
		
		
		
	},
	computed:{
		...mapGetters(['isToken']),
		collected(){
			return this.isCollectIng ? '已收藏' : '收藏' 
		},
		dealImage(){
			return this.storeInfo.shop_info ? replaceImage(this.storeInfo.shop_info.shop_logo) : ''
		}
	}
}
